@use 'base/mixins' as m;
@use 'base/variables' as v;

.places{
    h2{
        text-align: center;
    }
    &__grid{
        display: grid;
        grid-template-columns: repeat(4, 30rem);
        column-gap: 4rem;
        @include m.scrollHorizontal;

        @include m.tablet {
            @include m.grid(2, 2rem ) ;
        }
        @include m.desktop {
            @include m.grid(4, 2rem ) ;
        }

    }
}
.place{
    img{
        border-radius: 1rem;
    }
    &__name{
        font-size: 1.8rem;
    }
}