@use 'base/mixins' as m;
@use 'base/variables' as v;

.destinations{
    &__grid{
        @include m.grid(1, 2rem ) ;

        @include m.tablet {
            @include m.grid(2, 2rem ) ;
        }
        @include m.desktop {
            @include m.grid(4, 2rem ) ;
        }
    }
}

.destination{
    display: flex;
    justify-content:space-evenly;
    align-items: center;
    gap: 3rem ;


    &__img{
        width: 14rem;
    }
    &__info{

    }
    &__name,
    &__time{
        margin: 0;
    }
}