@use 'base/mixins' as m;
@use 'base/variables' as v;

.header{
    @include m.bgImage('../img/header.jpg');
    background-position: center;
    padding: 3rem 0 15rem 0;

    &__search{
        margin-top: 4rem;
        text-align: center;

        p{
            color: v.$white;
        }
        @include m.tablet {
            margin-top: 15rem;
        }
    }
    &__search-btn{
       @include m.btn( v.$white, v.$primary ) ;
    }
}

