@use 'base/mixins' as m;
@use 'base/variables' as v;

.experiences{
    
    &__list{
        display: grid;
        grid-template-columns: repeat(3, 30rem);
        column-gap: 2rem;
        align-items: center;
        @include m.scrollHorizontal;
        @include m.tablet {
            @include m.grid(3, 2rem) ;
        }
    }
}

.experience{
    text-align: center;
    img{
        border-radius: 1rem;
        max-height: 40rem;
        max-width: 30rem;
        margin: 0 auto;
    }   
    &__text{
        font-size: 1.8rem;
    }

    &__title{
        font-weight: 700;
        font-size: 2rem;
    }
}