h2{
    font-size:3.6rem; 
}
h3 {
    font-size: 3.2rem;
}

.section{
    padding: 3rem 0;

    @include m.tablet {
        padding: 8rem 0;
    }

}

[class$="__title"]{
    margin: 2rem 0;
}