@use 'base/mixins' as m;
@use 'base/variables' as v;

.bar{

    display: none;
    @include m.tablet {
        display:flex;
        justify-content: space-between;
    }
    &__navbar{

        @include m.tablet {
            display: flex;
            gap: 4rem;
            align-items: center;
        }
    }
    &__link{
        color: v.$white;
        transition: 0.3s;
        &:hover{
            opacity: 0.5;
        }
    }
    &__name{
        color: v.$white;
        margin: 0;
        font-size: 2rem;
    }
}