@use 'base/mixins' as m;
@use 'base/variables' as v;

.host{
    display: grid;
    grid-template-rows: repeat(2, 1fr);
    row-gap: 15rem;
    padding: 5rem;
    background-image: linear-gradient(to bottom, v.$black 0%, v.$black 50%, transparent 50%), url(../img/host.jpg);
    background-size: 100%, 60rem;
    background-repeat: no-repeat;

    @include m.phone {
        background-size: 100% 70rem;
        background-position: center, center 200%;
    }

    @include m.tablet {
        grid-template-rows: unset;
        background-image: linear-gradient(to right, v.$black 0%, v.$black 45%, transparent 50%), url(../img/host.jpg);
        background-size: 100%, auto;
        background-position: center center;
        padding: 15rem 5rem;
    }
    @include m.desktop {
        background-position: center;
    }

    &__content{
        @include m.tablet {
            max-width: 50%
        }

    }
    &__text{
        color: v.$white ;
    }
    &__btn{
        @include m.btn(v.$white, v.$black ) ;
    }
}