body {
  font-family: "Lexend", sans-serif;
  font-size: 1.6rem;
  /*     &.body-scroll{
          padding-top: 8.6rem;
      } */
}
body.fix-body {
  overflow: hidden;
}

p {
  color: #000000;
  font-size: 1.6rem;
  line-height: 2;
}

a {
  text-decoration: none;
  color: #000000;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.container {
  width: min(90%, 120rem);
  margin: 0 auto;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

/* h2{
    font-size: 3.6rem;
    display: inline-block;
