    color: v.$secondary;

    @include m.tablet {
        margin-top: 0;
    }

    &::after{
        content: '';
        background-color: v.$primary ;
        height: .7rem;
        width: 101%;
        display: block;
        margin: -1.4rem 0 0 0;
    }
}
 */
h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

.section {
  padding: 3rem 0;
}
@media (min-width: 768px) {
  .section {
    padding: 8rem 0;
  }
}

[class$=__title] {
  margin: 2rem 0;
}

.header {
  background-image: linear-gradient(rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), url("../img/header.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding: 3rem 0 15rem 0;
}
.header__search {
  margin-top: 4rem;
  text-align: center;
}
.header__search p {
  color: #FFFFFF;
}
@media (min-width: 768px) {
  .header__search {
    margin-top: 15rem;
  }
}
.header__search-btn {
  background-color: #FFFFFF;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #e8505b;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
}
.header__search-btn:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .header__search-btn {
    width: auto;
    display: inline-block;
  }
}

.form {
  margin: 5rem auto 2rem auto;
  background-color: #FFFFFF;
  border-radius: 5rem;
  box-shadow: 0px 3px 7px 0px #686868;
  padding: 3rem 0;
}
@media (min-width: 992px) {
  .form {
    display: flex;
    justify-content: space-between;
    max-width: 90rem;
    padding: 0;
  }
}
.form__field {
  padding: 1rem 2rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  transition: 0.3s;
}
.form__field:hover {
  background-color: #f0f0f0;
  border-radius: 5rem;
  box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.4);
}
@media (min-width: 992px) {
  .form__field:nth-child(1) {
    padding-left: 4rem;
  }
}
.form__label {
  font-size: 1.2rem;
  font-weight: 900;
}
.form__input {
  border: none;
  width: 100%;
  color: #686868;
}
.form__submit {
  display: block;
  height: 4rem;
  border: none;
  border-radius: 2rem;
  background-image: radial-gradient(circle, rgb(255, 56, 92), #bd1e59 100%);
  padding: 0 2rem;
  color: #FFFFFF;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 1.2rem;
}

.bar {
  display: none;
}
@media (min-width: 768px) {
  .bar {
    display: flex;
    justify-content: space-between;
  }
}
@media (min-width: 768px) {
  .bar__navbar {
    display: flex;
    gap: 4rem;
    align-items: center;
  }
}
.bar__link {
  color: #FFFFFF;
  transition: 0.3s;
}
.bar__link:hover {
  opacity: 0.5;
}
.bar__name {
  color: #FFFFFF;
  margin: 0;
  font-size: 2rem;
}

.destinations__grid {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 2rem;
}
@media (min-width: 768px) {
  .destinations__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .destinations__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.destination {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 3rem;
}
.destination__img {
  width: 14rem;
}
.destination__name, .destination__time {
  margin: 0;
}

.places h2 {
  text-align: center;
}
.places__grid {
  display: grid;
  grid-template-columns: repeat(4, 30rem);
  column-gap: 4rem;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.places__grid > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .places__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .places__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

.place img {
  border-radius: 1rem;
}
.place__name {
  font-size: 1.8rem;
}

.host {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 15rem;
  padding: 5rem;
  background-image: linear-gradient(to bottom, #000000 0%, #000000 50%, transparent 50%), url(../img/host.jpg);
  background-size: 100%, 60rem;
  background-repeat: no-repeat;
}
@media (min-width: 480px) {
  .host {
    background-size: 100% 70rem;
    background-position: center, center 200%;
  }
}
@media (min-width: 768px) {
  .host {
    grid-template-rows: unset;
    background-image: linear-gradient(to right, #000000 0%, #000000 45%, transparent 50%), url(../img/host.jpg);
    background-size: 100%, auto;
    background-position: center center;
    padding: 15rem 5rem;
  }
}
@media (min-width: 992px) {
  .host {
    background-position: center;
  }
}
@media (min-width: 768px) {
  .host__content {
    max-width: 50%;
  }
}
.host__text {
  color: #FFFFFF;
}
.host__btn {
  background-color: #FFFFFF;
  font-size: 2rem;
  padding: 1.2rem 3rem;
  border-radius: 1rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #000000;
  text-decoration: none;
  transition: 0.3s ease;
  border: none;
}
.host__btn:hover {
  cursor: pointer;
  opacity: 0.5;
  color: gray;
}
@media (min-width: 768px) {
  .host__btn {
    width: auto;
    display: inline-block;
  }
}

.experiences__list {
  display: grid;
  grid-template-columns: repeat(3, 30rem);
  column-gap: 2rem;
  align-items: center;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-snap-type: x mandatory;
}
.experiences__list > * {
  scroll-snap-align: center;
}
@media (min-width: 768px) {
  .experiences__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
  }
}

.experience {
  text-align: center;
}
.experience img {
  border-radius: 1rem;
  max-height: 40rem;
  max-width: 30rem;
  margin: 0 auto;
}
.experience__text {
  font-size: 1.8rem;
}
.experience__title {
  font-weight: 700;
  font-size: 2rem;
}

.footer {
  background-color: #f3f3f3;
  border-top: 1px solid #cecece;
  padding-top: 5rem;
}
.footer__widget {
  text-align: center;
}
@media (min-width: 768px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 2rem;
  }
}
@media (min-width: 992px) {
  .footer__grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}
.footer__title {
  text-transform: uppercase;
  font-size: 1.6rem;
}
.footer__link {
  display: block;
  color: #686868;
  margin-bottom: 1rem;
  transition: 0.3s;
}
.footer__link:hover {
  opacity: 0.5;
}
.footer__bar {
  border-top: 1px solid #cecece;
  margin-top: 2rem;
  padding: 2rem;
}
.footer__navbar {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  align-items: center;
}
@media (min-width: 768px) {
  .footer__navbar {
    flex-direction: row;
    justify-content: center;
  }
}
.footer__link--bar::before {
  content: " - ";
  margin-right: 1.5rem;
}
.footer__copyright {
  margin: 0;
  text-align: center;
  font-weight: 700;
}