@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer{
    background-color: lighten(v.$ligthGray, 7%) ;
    border-top: 1px solid lighten(v.$gray, 40%);
    padding-top: 5rem;
    &__widget{
        text-align: center
    }
    &__grid{
        @include m.tablet {
            @include m.grid(2,2rem ) ;
        }
        @include m.desktop {
            @include m.grid(4, 2rem ) ;
        }

    }
    &__title{
        text-transform: uppercase;
        font-size: 1.6rem;
    }

    &__link{
        display: block;
        color: v.$gray;
        margin-bottom: 1rem;
        transition: 0.3s;
        &:hover{
            opacity: 0.5;
        }
    }
    &__bar{
        border-top: 1px solid lighten(v.$gray, 40%);
        margin-top: 2rem;
        padding: 2rem;
    }
    &__navbar{
        display: flex;
        flex-direction: column;
        gap: 2rem;
        align-items: center;
        @include m.tablet {
            flex-direction: row;
            justify-content: center;
        }
    }
    &__link--bar{
        &::before{
            content: ' - ';
            margin-right: 1.5rem;
        }
    }
    &__copyright{
        margin: 0;
        text-align: center;
        font-weight: 700;

    }
}